import React, { useState } from 'react';

function OrderForm({ products, onSubmit, onCancel, lastOrderNumber }) {
  const [orderItems, setOrderItems] = useState([{
    productId: '',
    quantity: 1,
    sellingPrice: ''
  }]);
  const [shippingType, setShippingType] = useState('normal'); // normal 或 remote

  // 邮费配置
  const shippingFees = {
    normal: 12,
    remote: 22
  };

  const handleAddItem = () => {
    setOrderItems([...orderItems, {
      productId: '',
      quantity: 1,
      sellingPrice: ''
    }]);
  };

  const handleRemoveItem = (index) => {
    setOrderItems(orderItems.filter((_, i) => i !== index));
  };

  const handleItemChange = (index, field, value) => {
    const newItems = [...orderItems];
    newItems[index] = { ...newItems[index], [field]: value };
    setOrderItems(newItems);
  };

  const calculateTotals = () => {
    const totals = orderItems.reduce((acc, item) => {
      const product = products.find(p => p.id === item.productId);
      if (product) {
        acc.totalCost += product.costPrice * item.quantity;
        acc.totalRevenue += Number(item.sellingPrice) * item.quantity;
      }
      return acc;
    }, { totalCost: 0, totalRevenue: 0 });

    const shippingFee = shippingFees[shippingType];
    const profit = totals.totalRevenue - totals.totalCost - shippingFee;

    return {
      ...totals,
      shippingFee,
      profit
    };
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const totals = calculateTotals();
    
    onSubmit({
      id: String(lastOrderNumber + 1).padStart(4, '0'),
      date: new Date().toISOString(),
      items: orderItems,
      shippingType,
      ...totals
    });
  };

  return (
    <form onSubmit={handleSubmit} className="order-form">
      <h2>创建订单</h2>
      
      {orderItems.map((item, index) => (
        <div key={index} className="order-item">
          <div className="item-header">
            <h3>商品 {index + 1}</h3>
            {orderItems.length > 1 && (
              <button 
                type="button" 
                onClick={() => handleRemoveItem(index)}
                className="remove-item"
              >
                删除
              </button>
            )}
          </div>
          
          <div className="item-fields">
            <label>
              选择商品：
              <select
                value={item.productId}
                onChange={(e) => handleItemChange(index, 'productId', e.target.value)}
                required
              >
                <option value="">请选择商品</option>
                {products.map(product => (
                  <option key={product.id} value={product.id}>
                    {product.name} (成本: ¥{product.costPrice})
                  </option>
                ))}
              </select>
            </label>

            <label>
              数量：
              <input
                type="number"
                min="1"
                value={item.quantity}
                onChange={(e) => handleItemChange(index, 'quantity', Number(e.target.value))}
                required
              />
            </label>

            <label>
              售价：
              <input
                type="number"
                min="0"
                step="0.01"
                value={item.sellingPrice}
                onChange={(e) => handleItemChange(index, 'sellingPrice', e.target.value)}
                required
              />
            </label>
          </div>
        </div>
      ))}

      <button type="button" onClick={handleAddItem} className="add-item">
        添加商品
      </button>

      <div className="shipping-section">
        <label>
          配送类型：
          <select
            value={shippingType}
            onChange={(e) => setShippingType(e.target.value)}
          >
            <option value="normal">普通地区 (¥{shippingFees.normal})</option>
            <option value="remote">偏远地区 (¥{shippingFees.remote})</option>
          </select>
        </label>
      </div>

      <div className="order-summary">
        <h3>订单汇总</h3>
        {(() => {
          const totals = calculateTotals();
          return (
            <>
              <p>总成本: ¥{totals.totalCost}</p>
              <p>总收入: ¥{totals.totalRevenue}</p>
              <p>邮费: ¥{totals.shippingFee}</p>
              <p className="profit">预计利润: ¥{totals.profit}</p>
            </>
          );
        })()}
      </div>

      <div className="form-buttons">
        <button type="submit">提交订单</button>
        <button type="button" onClick={onCancel} className="cancel-button">
          取消
        </button>
      </div>
    </form>
  );
}

export default OrderForm; 