import React, { useState } from 'react';

function OrderList({ orders, products }) {
  const [searchTerm, setSearchTerm] = useState('');
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [sortBy, setSortBy] = useState('date'); // 'date', 'profit'

  // 筛选和排序订单
  const filteredOrders = orders
    .filter(order => {
      // 日期筛选
      const orderDate = new Date(order.date);
      const isAfterStart = !startDate || orderDate >= new Date(startDate);
      const isBeforeEnd = !endDate || orderDate <= new Date(endDate);
      
      // 搜索筛选
      const product = products.find(p => p.id === order.items[0].productId);
      const matchesSearch = !searchTerm || 
        (product && product.name.toLowerCase().includes(searchTerm.toLowerCase()));

      return isAfterStart && isBeforeEnd && matchesSearch;
    })
    .sort((a, b) => {
      if (sortBy === 'date') {
        return new Date(b.date) - new Date(a.date);
      }
      return b.profit - a.profit;
    });

  // 计算统计数据
  const stats = filteredOrders.reduce((acc, order) => {
    acc.totalOrders++;
    acc.totalProfit += order.profit;
    acc.totalRevenue += order.items[0].sellingPrice * order.items[0].quantity;
    return acc;
  }, { totalOrders: 0, totalProfit: 0, totalRevenue: 0 });

  return (
    <div className="order-list">
      <h2>订单列表</h2>
      
      <div className="order-search">
        <div className="search-row">
          <input
            type="text"
            placeholder="搜索商品名称..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="search-input"
          />
          <select 
            value={sortBy} 
            onChange={(e) => setSortBy(e.target.value)}
            className="sort-select"
          >
            <option value="date">按日期排序</option>
            <option value="profit">按利润排序</option>
          </select>
        </div>

        <div className="date-filters">
          <label>
            开始日期：
            <input
              type="date"
              value={startDate}
              onChange={(e) => setStartDate(e.target.value)}
            />
          </label>
          <label>
            结束日期：
            <input
              type="date"
              value={endDate}
              onChange={(e) => setEndDate(e.target.value)}
            />
          </label>
        </div>

        <div className="order-stats">
          <div className="stat-item">
            <span className="stat-label">订单数量</span>
            <span className="stat-value">{stats.totalOrders}</span>
          </div>
          <div className="stat-item">
            <span className="stat-label">总收入</span>
            <span className="stat-value">¥{stats.totalRevenue.toFixed(2)}</span>
          </div>
          <div className="stat-item">
            <span className="stat-label">总利润</span>
            <span className="stat-value profit">¥{stats.totalProfit.toFixed(2)}</span>
          </div>
        </div>
      </div>

      <div className="order-grid">
        {filteredOrders.map(order => {
          const product = products.find(p => p.id === order.items[0].productId);
          return (
            <div key={order.id} className="order-card">
              <div className="order-header">
                <span className="order-date">{order.date}</span>
                <span className="order-id">#{order.id}</span>
              </div>
              
              <div className="order-items">
                <h3>商品明细</h3>
                <ul>
                  <li>
                    <span>{product ? product.name : '未知商品'}</span>
                    <span className="item-price">
                      ¥{order.items[0].sellingPrice} × {order.items[0].quantity}
                    </span>
                  </li>
                </ul>
              </div>

              <div className="order-details">
                <div className="detail-row">
                  <span>邮费：</span>
                  <span>¥{order.shippingFee}</span>
                </div>
                <div className="detail-row profit">
                  <span>利润：</span>
                  <span>¥{order.profit}</span>
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default OrderList; 