import React, { useState } from 'react';

function StockLogs({ logs }) {
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [searchTerm, setSearchTerm] = useState('');

  // 筛选日志
  const filteredLogs = logs
    .filter(log => {
      const logDate = new Date(log.date);
      const isAfterStart = !startDate || logDate >= new Date(startDate);
      const isBeforeEnd = !endDate || logDate <= new Date(endDate);
      const matchesSearch = !searchTerm || 
        log.productName.toLowerCase().includes(searchTerm.toLowerCase());

      return isAfterStart && isBeforeEnd && matchesSearch;
    })
    .sort((a, b) => new Date(b.date) - new Date(a.date));

  // 计算统计数据
  const stats = filteredLogs.reduce((acc, log) => {
    acc.totalChanges++;
    acc.totalIncrease += log.change > 0 ? log.change : 0;
    acc.totalDecrease += log.change < 0 ? -log.change : 0;
    return acc;
  }, { totalChanges: 0, totalIncrease: 0, totalDecrease: 0 });

  return (
    <div className="stock-logs">
      <h2>库存变更记录</h2>
      
      <div className="logs-filter">
        <input
          type="text"
          placeholder="搜索商品名称..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
        <input
          type="date"
          value={startDate}
          onChange={(e) => setStartDate(e.target.value)}
        />
        <input
          type="date"
          value={endDate}
          onChange={(e) => setEndDate(e.target.value)}
        />
      </div>

      <div className="logs-stats">
        <div>总变更次数：{stats.totalChanges}</div>
        <div>总入库数量：{stats.totalIncrease}</div>
        <div>总出库数量：{stats.totalDecrease}</div>
      </div>

      <table className="logs-table">
        <thead>
          <tr>
            <th>时间</th>
            <th>商品名称</th>
            <th>变更类型</th>
            <th>变更数量</th>
            <th>变更前库存</th>
            <th>变更后库存</th>
          </tr>
        </thead>
        <tbody>
          {filteredLogs.map(log => (
            <tr key={log.id}>
              <td>{new Date(log.date).toLocaleString()}</td>
              <td>{log.productName}</td>
              <td>{log.type}</td>
              <td className={log.change > 0 ? 'stock-increase' : 'stock-decrease'}>
                {log.change > 0 ? `+${log.change}` : log.change}
              </td>
              <td>{log.previousStock}</td>
              <td>{log.newStock}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default StockLogs; 