import React, { useMemo } from 'react';

function Dashboard({ orders, products }) {
  const stats = useMemo(() => {
    const today = new Date();
    const thisMonth = today.getMonth();
    const thisYear = today.getFullYear();

    const monthOrders = orders.filter(order => {
      const orderDate = new Date(order.date);
      return orderDate.getMonth() === thisMonth && orderDate.getFullYear() === thisYear;
    });

    const totalProfit = orders.reduce((sum, order) => sum + order.profit, 0);
    const monthlyProfit = monthOrders.reduce((sum, order) => sum + order.profit, 0);
    
    const productStats = products.reduce((acc, product) => {
      const productOrders = orders.filter(order => 
        order.items.some(item => item.productId === product.id)
      );
      const totalSales = productOrders.reduce((sum, order) => {
        const item = order.items.find(item => item.productId === product.id);
        return sum + (item ? item.quantity : 0);
      }, 0);

      acc[product.id] = {
        name: product.name,
        totalSales,
        totalProfit: productOrders.reduce((sum, order) => {
          const item = order.items.find(item => item.productId === product.id);
          if (item) {
            const itemProfit = (item.sellingPrice - product.costPrice) * item.quantity;
            return sum + itemProfit;
          }
          return sum;
        }, 0)
      };
      return acc;
    }, {});

    const bestSeller = Object.values(productStats)
      .sort((a, b) => b.totalSales - a.totalSales)[0];

    const mostProfitable = Object.values(productStats)
      .sort((a, b) => b.totalProfit - a.totalProfit)[0];

    return {
      totalOrders: orders.length,
      monthlyOrders: monthOrders.length,
      totalProfit: totalProfit.toFixed(2),
      monthlyProfit: monthlyProfit.toFixed(2),
      averageProfit: orders.length ? (totalProfit / orders.length).toFixed(2) : '0.00',
      bestSeller,
      mostProfitable
    };
  }, [orders, products]);

  return (
    <div className="dashboard">
      <h2>数据统计</h2>
      
      <div className="stat-cards">
        <div className="stat-card">
          <h3>订单统计</h3>
          <div className="stat-row">
            <span>总订单数：</span>
            <span>{stats.totalOrders}</span>
          </div>
          <div className="stat-row">
            <span>本月订单：</span>
            <span>{stats.monthlyOrders}</span>
          </div>
        </div>

        <div className="stat-card">
          <h3>利润统计</h3>
          <div className="stat-row">
            <span>总利润：</span>
            <span className="profit">¥{stats.totalProfit}</span>
          </div>
          <div className="stat-row">
            <span>本月利润：</span>
            <span className="profit">¥{stats.monthlyProfit}</span>
          </div>
          <div className="stat-row">
            <span>平均利润：</span>
            <span>¥{stats.averageProfit}</span>
          </div>
        </div>

        <div className="stat-card">
          <h3>商品分析</h3>
          {stats.bestSeller && (
            <div className="stat-row">
              <span>销量最佳：</span>
              <span>{stats.bestSeller.name} ({stats.bestSeller.totalSales}件)</span>
            </div>
          )}
          {stats.mostProfitable && (
            <div className="stat-row">
              <span>最高利润：</span>
              <span>{stats.mostProfitable.name} (¥{stats.mostProfitable.totalProfit.toFixed(2)})</span>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default Dashboard; 