import React from 'react';
import * as XLSX from 'xlsx';

function DataManager({ products, orders, onImport, onClear }) {
  // 导入成本表
  const handleCostImport = (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const data = new Uint8Array(e.target.result);
        const workbook = XLSX.read(data, { type: 'array' });
        const worksheet = workbook.Sheets[workbook.SheetNames[0]];

        const jsonData = XLSX.utils.sheet_to_json(worksheet, {
          raw: false,
          dateNF: 'yyyy-mm-dd',
          defval: ''
        });

        console.log('成本表原始数据:', jsonData);

        const processedProducts = jsonData.map((row, index) => ({
          id: String(index + 1),
          date: row['日期'] || new Date().toISOString().split('T')[0],
          name: row['规格'] || '',
          costPrice: parseFloat(row['成本价']) || 0,
          promotionFee: parseFloat(row['推广费']) || 0,
          description: row['描述'] || '',
          stock: Number(row['库存']) || 0,
          stockAlert: Number(row['预警值']) || 0,
          supplier: row['供应商'] || '',
          location: row['位置'] || ''
        }));

        console.log('处理后的成本表数据:', processedProducts);

        onImport({
          products: processedProducts,
          orders: orders // 保持原有订单
        });

        e.target.value = '';
        alert('成本表导入成功！');
      } catch (error) {
        console.error('成本表导入错误:', error);
        alert('成本表导入失败，请检查文件格式');
      }
    };

    reader.readAsArrayBuffer(file);
  };

  // 导入流水表
  const handleOrderImport = (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const data = new Uint8Array(e.target.result);
        const workbook = XLSX.read(data, { type: 'array' });
        const worksheet = workbook.Sheets[workbook.SheetNames[0]];

        const jsonData = XLSX.utils.sheet_to_json(worksheet, {
          raw: false,
          dateNF: 'yyyy-mm-dd',
          defval: ''
        });

        console.log('流水表原始数据:', jsonData);

        const processedOrders = jsonData.map((row, index) => {
          // 根据规格查找对应的商品
          const product = products.find(p => p.name === row['规格']);
          const date = row['日期'] ? new Date(row['日期']).toISOString().split('T')[0] : new Date().toISOString().split('T')[0];
          const quantity = parseInt(row['数量']) || 1;
          const sellingPrice = parseFloat(row['售价']) || 0;
          const shippingFee = parseFloat(row['邮费']) || 0;

          const order = {
            id: String(index + 1).padStart(4, '0'),
            date: date,
            items: [{
              productId: product ? product.id : '',
              quantity: quantity,
              sellingPrice: sellingPrice
            }],
            shippingFee: shippingFee
          };

          // 计算利润
          const costPrice = product ? product.costPrice : 0;
          const promotionFee = product ? product.promotionFee : 0;
          order.profit = (sellingPrice * quantity) - (costPrice * quantity) - shippingFee - promotionFee;

          console.log('处理订单:', order);
          return order;
        });

        console.log('处理后的流水表数据:', processedOrders);

        onImport({
          products: products,
          orders: processedOrders
        });

        e.target.value = '';
        alert('流水表导入成功！');
      } catch (error) {
        console.error('流水表导入错误:', error);
        alert('流水表导入失败，请检查文件格式');
      }
    };

    reader.readAsArrayBuffer(file);
  };

  // 导出成本表
  const handleCostExport = () => {
    try {
      const exportData = products.map(product => ({
        '日期': product.date,
        '规格': product.name,
        '成本价': product.costPrice,
        '推广费': product.promotionFee,
        '描述': product.description,
        '库存': product.stock || 0  // 只保留库存字段
      }));

      const wb = XLSX.utils.book_new();
      const ws = XLSX.utils.json_to_sheet(exportData, {
        header: ['日期', '规格', '成本价', '推广费', '描述', '库存']
      });

      // 设置列宽
      const colWidths = [
        { wch: 15 }, // 日期
        { wch: 20 }, // 规格
        { wch: 10 }, // 成本价
        { wch: 10 }, // 推广费
        { wch: 20 }, // 描述
        { wch: 10 }  // 库存
      ];
      ws['!cols'] = colWidths;

      XLSX.utils.book_append_sheet(wb, ws, "成本表");
      XLSX.writeFile(wb, "成本表.xlsx");
    } catch (error) {
      console.error('成本表导出错误:', error);
      alert('成本表导出失败');
    }
  };

  // 导出流水表
  const handleOrderExport = () => {
    try {
      const exportData = orders.map(order => {
        const product = products.find(p => p.id === order.items[0].productId);
        return {
          '日期': order.date,
          '规格': product ? product.name : '',
          '数量': order.items[0].quantity,
          '售价': order.items[0].sellingPrice,
          '邮费': order.shippingFee,
          '利润': order.profit
        };
      });

      // 创建工作簿和工作表
      const wb = XLSX.utils.book_new();
      const ws = XLSX.utils.json_to_sheet(exportData, {
        header: ['日期', '规格', '数量', '售价', '邮费', '利润']
      });

      // 设置列宽
      const colWidths = [
        { wch: 15 }, // 日期
        { wch: 20 }, // 规格
        { wch: 10 }, // 数量
        { wch: 10 }, // 售价
        { wch: 10 }, // 邮费
        { wch: 10 }  // 利润
      ];
      ws['!cols'] = colWidths;

      XLSX.utils.book_append_sheet(wb, ws, "流水表");
      XLSX.writeFile(wb, "流水表.xlsx");
    } catch (error) {
      console.error('流水表导出错误:', error);
      alert('流水表导出失败');
    }
  };

  return (
    <div className="data-manager">
      <div className="import-section">
        <h3>导入数据</h3>
        <div>
          <input
            type="file"
            accept=".xlsx,.xls"
            onChange={handleCostImport}
            className="file-input"
          />
          <p className="help-text">导入成本表</p>
        </div>
        <div>
          <input
            type="file"
            accept=".xlsx,.xls"
            onChange={handleOrderImport}
            className="file-input"
          />
          <p className="help-text">导入流水表</p>
        </div>
      </div>

      <div className="export-section">
        <h3>导出数据</h3>
        <button onClick={handleCostExport} className="export-button">
          导出成本表
        </button>
        <button onClick={handleOrderExport} className="export-button">
          导出流水表
        </button>
        <button onClick={onClear} className="export-button clear-button">
          清除数据
        </button>
      </div>
    </div>
  );
}

export default DataManager; 