import React from 'react';

function ProductList({ products, onEdit, onDelete }) {
  console.log('ProductList 接收到的数据:', products);

  return (
    <div className="product-list">
      <h2>商品列表</h2>
      <table>
        <thead>
          <tr>
            <th>日期</th>
            <th>规格</th>
            <th>成本价</th>
            <th>推广费</th>
            <th>库存</th>
            <th>描述</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody>
          {products.map(product => (
            <tr key={product.id}>
              <td>{product.date}</td>
              <td>{product.name}</td>
              <td>¥{product.costPrice}</td>
              <td>¥{product.promotionFee}</td>
              <td>{product.stock || 0}</td>
              <td>{product.description}</td>
              <td>
                <button onClick={() => onEdit(product)} className="edit-button">
                  编辑
                </button>
                <button onClick={() => onDelete(product.id)} className="delete-button">
                  删除
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      {products.length === 0 && (
        <p className="no-data">暂无商品数据</p>
      )}
    </div>
  );
}

export default ProductList; 