import React, { useState } from 'react';

function StockManager({ products, onUpdateStock }) {
  const [stockChanges, setStockChanges] = useState({});

  // 处理库存变更
  const handleStockChange = (productId, change) => {
    setStockChanges(prev => ({
      ...prev,
      [productId]: (prev[productId] || 0) + change
    }));
  };

  // 提交库存变更
  const handleSubmit = () => {
    const updates = Object.entries(stockChanges).map(([productId, change]) => ({
      productId,
      change: Number(change),
      date: new Date().toISOString().split('T')[0]
    }));

    onUpdateStock(updates);
    setStockChanges({});
  };

  return (
    <div className="stock-manager">
      <h2>库存管理</h2>
      
      <div className="stock-list">
        {products.map(product => (
          <div key={product.id} className="stock-item">
            <div className="stock-info">
              <h3>{product.name}</h3>
              <div className="stock-details">
                <span className="current-stock">
                  当前库存: {product.stock || 0}
                </span>
              </div>
            </div>
            
            <div className="stock-actions">
              <button onClick={() => handleStockChange(product.id, -1)}>-</button>
              <input
                type="number"
                className="stock-input"
                value={stockChanges[product.id] || 0}
                onChange={(e) => setStockChanges({
                  ...stockChanges,
                  [product.id]: Number(e.target.value)
                })}
              />
              <button onClick={() => handleStockChange(product.id, 1)}>+</button>
            </div>
          </div>
        ))}
      </div>

      {Object.keys(stockChanges).length > 0 && (
        <div className="stock-submit">
          <button onClick={handleSubmit}>
            提交库存变更
          </button>
        </div>
      )}
    </div>
  );
}

export default StockManager; 