import React, { useState } from 'react';

function AddProductForm({ onSubmit }) {
  const [product, setProduct] = useState({
    name: '',           // 规格
    costPrice: '',      // 成本价
    promotionFee: '',   // 推广费
    description: '',    // 描述
    date: new Date().toISOString().split('T')[0], // 日期
    stock: ''          // 库存数量
  });

  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit({
      ...product,
      id: Date.now().toString(),
      costPrice: Number(product.costPrice),
      promotionFee: Number(product.promotionFee),
      stock: Number(product.stock)
    });
    setProduct({
      name: '',
      costPrice: '',
      promotionFee: '',
      description: '',
      date: new Date().toISOString().split('T')[0],
      stock: ''
    });
  };

  return (
    <form onSubmit={handleSubmit}>
      <div>
        <label>规格：</label>
        <input
          type="text"
          value={product.name}
          onChange={(e) => setProduct({ ...product, name: e.target.value })}
          required
        />
      </div>
      <div>
        <label>成本价：</label>
        <input
          type="number"
          step="0.1"
          value={product.costPrice}
          onChange={(e) => setProduct({ ...product, costPrice: e.target.value })}
          required
        />
      </div>
      <div>
        <label>推广费：</label>
        <input
          type="number"
          step="0.1"
          value={product.promotionFee}
          onChange={(e) => setProduct({ ...product, promotionFee: e.target.value })}
          required
        />
      </div>
      <div>
        <label>库存数量：</label>
        <input
          type="number"
          value={product.stock}
          onChange={(e) => setProduct({ ...product, stock: e.target.value })}
          required
        />
      </div>
      <div>
        <label>描述：</label>
        <input
          type="text"
          value={product.description}
          onChange={(e) => setProduct({ ...product, description: e.target.value })}
        />
      </div>
      <div>
        <label>日期：</label>
        <input
          type="date"
          value={product.date}
          onChange={(e) => setProduct({ ...product, date: e.target.value })}
          required
        />
      </div>
      <button type="submit">添加商品</button>
    </form>
  );
}

export default AddProductForm; 