import React, { useState } from 'react';

function EditProductForm({ product, onSubmit, onCancel }) {
  const [editedProduct, setEditedProduct] = useState({
    ...product,
    costPrice: String(product.costPrice),
    promotionFee: String(product.promotionFee),
    stock: String(product.stock || 0)
  });

  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit({
      ...editedProduct,
      costPrice: Number(editedProduct.costPrice),
      promotionFee: Number(editedProduct.promotionFee),
      stock: Number(editedProduct.stock)
    });
  };

  return (
    <form onSubmit={handleSubmit}>
      <div>
        <label>规格：</label>
        <input
          type="text"
          value={editedProduct.name}
          onChange={(e) => setEditedProduct({ ...editedProduct, name: e.target.value })}
          required
        />
      </div>
      <div>
        <label>成本价：</label>
        <input
          type="number"
          step="0.1"
          value={editedProduct.costPrice}
          onChange={(e) => setEditedProduct({ ...editedProduct, costPrice: e.target.value })}
          required
        />
      </div>
      <div>
        <label>推广费：</label>
        <input
          type="number"
          step="0.1"
          value={editedProduct.promotionFee}
          onChange={(e) => setEditedProduct({ ...editedProduct, promotionFee: e.target.value })}
          required
        />
      </div>
      <div>
        <label>描述：</label>
        <input
          type="text"
          value={editedProduct.description}
          onChange={(e) => setEditedProduct({ ...editedProduct, description: e.target.value })}
        />
      </div>
      <div>
        <label>日期：</label>
        <input
          type="date"
          value={editedProduct.date}
          onChange={(e) => setEditedProduct({ ...editedProduct, date: e.target.value })}
          required
        />
      </div>
      <div>
        <label>库存数量：</label>
        <input
          type="number"
          value={editedProduct.stock}
          onChange={(e) => setEditedProduct({ ...editedProduct, stock: e.target.value })}
          required
        />
      </div>
      <div className="form-buttons">
        <button type="submit">保存</button>
        <button type="button" onClick={onCancel} className="cancel-button">
          取消
        </button>
      </div>
    </form>
  );
}

export default EditProductForm; 