import React, { useState, useEffect } from 'react';
import './App.css';
import ProductList from './components/ProductList';
import AddProductForm from './components/AddProductForm';
import EditProductForm from './components/EditProductForm';
import OrderForm from './components/OrderForm';
import OrderList from './components/OrderList';
import Dashboard from './components/Dashboard';
import DataManager from './components/DataManager';
import StockManager from './components/StockManager';
import StockLogs from './components/StockLogs';

function App() {
  // 从 localStorage 初始化状态
  const [products, setProducts] = useState(() => {
    const savedProducts = localStorage.getItem('products');
    return savedProducts ? JSON.parse(savedProducts) : [];
  });
  
  const [orders, setOrders] = useState(() => {
    const savedOrders = localStorage.getItem('orders');
    return savedOrders ? JSON.parse(savedOrders) : [];
  });

  const [isAddingProduct, setIsAddingProduct] = useState(false);
  const [editingProduct, setEditingProduct] = useState(null);
  const [isAddingOrder, setIsAddingOrder] = useState(false);
  const [stockLogs, setStockLogs] = useState(() => {
    const savedLogs = localStorage.getItem('stockLogs');
    return savedLogs ? JSON.parse(savedLogs) : [];
  });

  // 数据变化时保存到 localStorage
  useEffect(() => {
    try {
      localStorage.setItem('products', JSON.stringify(products));
      console.log('Products saved:', products); // 调试日志
    } catch (error) {
      console.error('Error saving products:', error);
    }
  }, [products]);

  useEffect(() => {
    try {
      localStorage.setItem('orders', JSON.stringify(orders));
      console.log('Orders saved:', orders); // 调试日志
    } catch (error) {
      console.error('Error saving orders:', error);
    }
  }, [orders]);

  // 保存库存日志到 localStorage
  useEffect(() => {
    try {
      localStorage.setItem('stockLogs', JSON.stringify(stockLogs));
    } catch (error) {
      console.error('Error saving stock logs:', error);
    }
  }, [stockLogs]);

  // 处理添加商品
  const handleAddProduct = (newProduct) => {
    try {
      const updatedProducts = [...products, newProduct];
      setProducts(updatedProducts);
      setIsAddingProduct(false);
      console.log('Product added:', newProduct); // 调试日志
    } catch (error) {
      console.error('Error adding product:', error);
    }
  };

  // 处理编辑商品
  const handleEditProduct = (product) => {
    setEditingProduct(product);
  };

  const handleEditSubmit = (updatedProduct) => {
    try {
      const updatedProducts = products.map(p => 
        p.id === updatedProduct.id ? updatedProduct : p
      );
      setProducts(updatedProducts);
      setEditingProduct(null);
      console.log('Product updated:', updatedProduct); // 调试日志
    } catch (error) {
      console.error('Error updating product:', error);
    }
  };

  // 处理删除商品
  const handleDeleteProduct = (productId) => {
    if (window.confirm('确定要删除这个商品吗？')) {
      try {
        const updatedProducts = products.filter(p => p.id !== productId);
        setProducts(updatedProducts);
        console.log('Product deleted:', productId); // 调试日志
      } catch (error) {
        console.error('Error deleting product:', error);
      }
    }
  };

  // 处理添加订单
  const handleAddOrder = (newOrder) => {
    try {
      // 检查库存是否足够
      const product = products.find(p => p.id === newOrder.items[0].productId);
      if (!product) {
        throw new Error('商品不存在');
      }

      const quantity = newOrder.items[0].quantity;
      if (product.stock < quantity) {
        throw new Error(`库存不足！当前库存: ${product.stock}`);
      }

      // 更新库存
      const updatedProducts = products.map(p => {
        if (p.id === product.id) {
          // 记录库存变更日志
          const newLog = {
            id: Date.now().toString(),
            date: new Date().toISOString(),
            productId: p.id,
            productName: p.name,
            previousStock: p.stock,
            change: -quantity,
            newStock: p.stock - quantity,
            type: '订单出库'
          };
          setStockLogs(prev => [...prev, newLog]);

          return {
            ...p,
            stock: p.stock - quantity
          };
        }
        return p;
      });

      // 添加订单
      const updatedOrders = [...orders, newOrder];
      setProducts(updatedProducts);
      setOrders(updatedOrders);
      setIsAddingOrder(false);
    } catch (error) {
      console.error('Error adding order:', error);
      alert(error.message);
    }
  };

  // 用于调试的组件挂载日志
  useEffect(() => {
    console.log('Component mounted with products:', products);
    console.log('Component mounted with orders:', orders);
  }, []);

  const getLastOrderNumber = () => {
    if (orders.length === 0) return 0;
    return Math.max(...orders.map(order => parseInt(order.id)));
  };

  const handleDataImport = (data) => {
    console.log('接收到导入数据:', data);
    
    if (data.products && Array.isArray(data.products)) {
      console.log('设置商品数据:', data.products);
      setProducts(data.products);
    }
    
    if (data.orders && Array.isArray(data.orders)) {
      console.log('设置订单数据:', data.orders);
      setOrders(data.orders);
    }
  };

  // 添加一个 effect 来监控 products 的变化
  useEffect(() => {
    console.log('products 状态更新:', products);
  }, [products]);

  const handleClearData = () => {
    setProducts([]);
    setOrders([]);
    localStorage.removeItem('products');
    localStorage.removeItem('orders');
  };

  // 处理库存更新
  const handleStockUpdate = (updates) => {
    try {
      const updatedProducts = products.map(product => {
        const update = updates.find(u => u.productId === product.id);
        if (update) {
          // 记录库存变更日志
          const newLog = {
            id: Date.now().toString(),
            date: new Date().toISOString(),
            productId: product.id,
            productName: product.name,
            previousStock: product.stock || 0,
            change: update.change,
            newStock: (product.stock || 0) + update.change,
            type: update.change > 0 ? '入库' : '出库'
          };
          setStockLogs(prev => [...prev, newLog]);

          // 更新商品库存
          return {
            ...product,
            stock: (product.stock || 0) + update.change,
            date: update.date // 更新日期为库存变更日期
          };
        }
        return product;
      });

      setProducts(updatedProducts);
      alert('库存更新成功！');
    } catch (error) {
      console.error('Error updating stock:', error);
      alert('库存更新失败');
    }
  };

  return (
    <div className="App">
      <header className="App-header">
        <h1>小豹管理系统</h1>
      </header>
      
      <main className="App-main">
        <DataManager 
          products={products}
          orders={orders}
          onImport={handleDataImport}
          onClear={handleClearData}
        />

        <Dashboard 
          orders={orders}
          products={products}
        />

        <div className="controls">
          <button 
            onClick={() => setIsAddingProduct(!isAddingProduct)}
            className="add-button"
          >
            {isAddingProduct ? '取消添加' : '添加新商品'}
          </button>
          <button 
            onClick={() => setIsAddingOrder(!isAddingOrder)}
            className="add-button order-button"
          >
            {isAddingOrder ? '取消订单' : '创建订单'}
          </button>
        </div>

        {isAddingProduct && (
          <AddProductForm onSubmit={handleAddProduct} />
        )}

        {editingProduct && (
          <EditProductForm 
            product={editingProduct}
            onSubmit={handleEditSubmit}
            onCancel={() => setEditingProduct(null)}
          />
        )}

        {isAddingOrder && (
          <OrderForm 
            products={products}
            onSubmit={handleAddOrder}
            onCancel={() => setIsAddingOrder(false)}
            lastOrderNumber={getLastOrderNumber()}
          />
        )}

        <ProductList 
          products={products} 
          onEdit={handleEditProduct}
          onDelete={handleDeleteProduct}
        />

        <OrderList 
          orders={orders}
          products={products}
        />

        <StockManager 
          products={products}
          onUpdateStock={handleStockUpdate}
        />
        <StockLogs logs={stockLogs} />
      </main>
    </div>
  );
}

export default App;
